import { Injectable } from "@angular/core";
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators'
import { CovidData } from '../models/covid-data';
import { DatePipe } from '@angular/common';

@Injectable({
    providedIn: 'root',
})

export class CovidDataService {

    private api = "https://corona.lmao.ninja/v2/countries/france?yesterday=true&strict=true&query=/";

    constructor(private http: HttpClient) {};

    getLiveData() : Observable<CovidData> {

        const url = `${this.api}`;
        return this.http.get(url)
            .pipe(map((res: any)  => {

                const liveData = res;
                const result : CovidData = {
                    code : liveData.countryInfo.iso2,
                    name : liveData.country,
                    date : new Date(liveData.updated),
                    total_cases : liveData.cases,
                    new_cases : liveData.todayCases,
                    recover : liveData.recovered,
                    death : liveData.deaths,
                    critical : liveData.critical
                };
                return result;
        }))
    }


}