import { Component, OnInit } from '@angular/core';
import { CovidData } from 'src/app/models/covid-data';
import { CovidDataService } from 'src/app/services/covid-data.services';
import {DecimalPipe} from '@angular/common';

@Component({
  selector: 'app-covid-data',
  templateUrl: './covid-data.component.html',
  styleUrls: ['./covid-data.component.css']
})
export class CovidDataComponent implements OnInit {

  data : CovidData;
  loaded = false;

  decimalPipe;

  constructor(private covidDataSerivce : CovidDataService, decimalPipe : DecimalPipe) { }

  ngOnInit(): void {

    this.covidDataSerivce.getLiveData().subscribe(data =>  {

        this.data = data;
        this.loaded = true;
    });
  }
}
