import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';
import {DecimalPipe} from '@angular/common';
import {LOCALE_ID } from '@angular/core';
import { registerLocaleData } from '@angular/common';
import localeFr from '@angular/common/locales/fr';
registerLocaleData(localeFr);

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { CovidDataService } from './services/covid-data.services';
import { WeatherDataService } from './services/weather-data.services';
import { CovidDataComponent } from './components/covid-data/covid-data.component';
import { WeatherDataComponent } from './components/weather-data/weather-data.component';

@NgModule({
  declarations: [
    AppComponent,
    CovidDataComponent,
    WeatherDataComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    HttpClientModule,
  ],
  providers: [   
      { provide: LOCALE_ID, useValue: 'fr-FR'},
      CovidDataService, 
      WeatherDataService,
      DecimalPipe
    ],
  bootstrap: [AppComponent]
})
export class AppModule { }
